// layout
$default-container-width: 1400;
$default-container-pad: rem(30);
$gnb-header-margin-top: 25;
$gnb-header-height: 131 - $gnb-header-margin-top;
$footer-top-margin: 150;

$bg-gray: #f8f8f8;
// footer
$bg-footer: #f9f9f9;
$bg-data: #f7fafc;

// color
$primary: #89b03f;
$primary-darken: #6b8138;
$secondary: #8a4933;
$tertiary: #e9bb5b;
$emphas: hsl(81, 47%, 47%);
$red: #ed1c24;
$light-gray: #bcbdc1;
$dark-gray: #333333;

// font color
$fc-default: #333333;
$fc-dark-gray: #58595b;
$fc-gray: #040405;
$fc-light-gray: #bcbdc1;
$fc-disabled-gray: #dddddd;
$fc-primary: $primary;
$fc-primary-darken: $primary-darken;
$fc-emphas: $emphas;
$fc-red: $red;

// font size
$fs-h1: 26;

// input
$input-outline: #e7e7e7;


// img-slot
$img-slot-bc: #e7e7e7;

// border
$border-color-gray: $light-gray
