@charset "UTF-8";
/* font-family: 'Noto Sans KR', sans-serif; */
@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, caption, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  font-weight: 400;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

hr,
legend {
  display: none;
  height: 0px;
}

a {
  color: #000000;
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

caption {
  font-size: 0;
  color: transparent;
  text-indent: -9999px;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}

label,
input,
select,
button {
  vertical-align: middle;
  outline: 0;
  box-sizing: border-box;
}

button {
  padding: 0;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

input {
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
}

select {
  appearance: none;
}

img {
  border: 0;
  line-height: 0;
  margin: 0;
  padding: 0;
  font-size: 0;
}

em {
  font-style: normal;
}

b {
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}

br {
  font-size: 0;
  opacity: 0;
}

input::-webkit-input-placeholder {
  color: #bcbdc1;
}

input::-moz-placeholder {
  color: #bcbdc1;
}

input:-moz-placeholder {
  color: #bcbdc1;
}

input:-ms-input-placeholder {
  color: #bcbdc1;
}

textarea::-webkit-input-placeholder {
  color: #bcbdc1;
}

textarea::-moz-placeholder {
  color: #bcbdc1;
}

textarea:-moz-placeholder {
  color: #bcbdc1;
}

textarea:-ms-input-placeholder {
  color: #bcbdc1;
}

.mo-visible {
  display: none;
}
@media all and (max-width: 670px) {
  .mo-visible {
    display: block;
  }
}

@media all and (max-width: 670px) {
  .pc-visible {
    display: none;
  }
}

html, body {
  scroll-behavior: smooth;
}
@media all and (max-width: 1920px) {
  html, body {
    font-size: 16px;
  }
}
@media all and (min-width: 1920px) {
  html, body {
    font-size: 16px;
  }
}

body, textarea {
  font-family: "Noto Sans KR", "맑은고딕", "Malgun Gothic", dotum, gulim, sans-serif;
  color: #333333;
}

@media all and (max-width: 1024px) {
  .for-pc {
    display: none !important;
  }
}

@media all and (min-width: 1024px) {
  .for-mobile {
    display: none !important;
  }
}

.hidden {
  display: none !important;
}

html.hold {
  overflow-y: hidden !important;
}

em, strong {
  font-weight: 700;
}

.color-green {
  color: #89b03f !important;
}

.color-red {
  color: #f01d11 !important;
}

.color-gray {
  color: #040405 !important;
}

.align-c {
  text-align: center;
}

.align-l {
  text-align: left;
}

.align-r {
  text-align: right;
}

.narmi-admin h2 {
  font-size: 1.625rem;
  margin-bottom: 0.625rem;
}

.table tbody.table-group-divider {
  border-top-width: 1px;
}

.table-topper, .table-footer {
  position: relative;
  display: flex;
  justify-content: space-between;
}
.table-topper .suffix, .table-topper .prefix, .table-footer .suffix, .table-footer .prefix {
  display: flex;
  flex: 1 1 auto;
  align-items: center;
}
.table-topper .prefix, .table-footer .prefix {
  justify-content: flex-start;
}
.table-topper .suffix, .table-footer .suffix {
  justify-content: flex-end;
}
.table-topper .center, .table-footer .center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}